.Searchwiki h1 {
    font-size: 3rem;
    margin-top: 0;
}

@media screen and (max-width: 650px) {
    .Searchwiki h1  {
        font-size: 2.5rem;
    }
}

.Searchwiki input {
    font-size: 1.2rem;
    padding: 3px 20px;
    width: 500px;
    max-width: 80%;
    border-radius: 5px;
    margin: 10px;
}

.btn {
    font-size: 1.2rem;
    font-family: inherit;
    border-radius: 5px;
    padding: 3px 20px;
}

.btn:hover {
    text-decoration: underline;
}

.btn--search {
    background-color: rgb(59, 23, 0);
    color: rgb(255, 244, 234);
}

.btn--random {
    margin-top: 1rem;
    margin-bottom: 1rem;
    color: rgb(59, 23, 0);
}
.Searchresult {
    background-color: rgba(255, 255, 255, 0.7);
    width: 80%;
    margin: 10px auto;
    padding: 10px 20px;
    text-align: left;
    border-radius: 5px;
    box-shadow: 0 1.5px 4px rgba(black, 0.2);
}

@media screen and (max-width: 650px) {
    .Searchresult {
        width: 90%;
        padding: 5px 15px;
    }
}

.Searchresult:hover {
    background-color: rgba(221, 217, 209, 0.9);
}

.Searchresult a {
    text-decoration: none;
        color: rgb(59, 23, 0);
}

.Searchresult h2 {
    font-size: 1.5rem;
    margin: 0.5rem;
}

@media screen and (max-width: 650px) {
    .Searchresult h2 {
    font-size: 1.3rem;
    margin: 0.5rem;
    }
}

.Searchresult p {
    font-size: 1.3rem;
    line-height: 1.5;
    margin: 0.5rem;
}

@media screen and (max-width: 650px) {
    .Searchresult p {
    font-size: 1.2rem;
    line-height: 1.3;
    margin: 0.5rem;
}
}
.Footer {
    -webkit-align-self: stretch;
            align-self: stretch;
    background-color: rgba(43, 43, 43, 0.8);
    color: rgb(230, 230, 230);
    font-size: 1.2rem;
    padding: 15px;   
}

.Footer a {
    text-decoration: none;
    color: rgb(160, 169, 255);
}

.Footer a:hover {
    text-decoration: underline;
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px);
}
